export default {
    name: "shopshow",
    data(){
        return{
            shop:[],
        }
    },
    mounted:function () {
        this.$axios.get('/business/detail?business_id=4&longitude=103.936376&latitude=30.786626').then(res=>{
            console.log(res.data.data);
            this.shop=res.data.data;
        })
    }
}



